.button__main {
  width: min(300px, 40%);
  background-image: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%);
  color: white;
  border-radius: 15px;
  padding: 5px 0;
  margin: 0 auto;
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.5);
  transition: 0.5s;
}

.button__main:hover {
  box-shadow: none;
}
