.popup__close-btn {
  position: absolute;
  width: 40px;
  height: 40px;
  padding: 0;
  margin: 0;
  border: none;
  top: -40px;
  right: -40px;
  background-image: url(../../../images/close-btn.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  background-color: transparent;
}

@media only screen and (max-width: 768px) {
  .popup__close-btn {
    width: 30px;
    height: 30px;
    top: -30px;
    right: -30px;
  }
}
